body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;    
  /* IE10 Consumer Preview */
 /* Mozilla Firefox */
  /* Opera */
  /* Webkit (Safari/Chrome 10) */ 
  background-image: -webkit-gradient(linear, left top, right top, color-stop(0, #4D4D4D), color-stop(1, #136363));
  /* Webkit (Chrome 11+) */ 
  background-image: -webkit-linear-gradient(left, #4D4D4D 0%, #136363 100%);
  /* W3C Markup, IE10 Release Preview */ 
  background-image: -webkit-gradient(linear, left top, right top, from(#4D4D4D), to(#136363)); 
  background-image: linear-gradient(to right, #4D4D4D 0%, #136363 100%);
  width: 100vw;
  height: 100vh;
  background-repeat: repeat;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.searchBar {
	padding: 5px;
	margin-bottom: 20px;
}

input {
	padding: 3px;
	background-color: rgba(0, 111, 138, 0.2);
	color: black;
	border: solid 2px rgb(0, 73, 91);
	border-radius: 5px;
	height: 38px;
	width: 250px;
	text-align: center;
	font-weight: 730;
}

.robo-card {
	text-align: center;
	display: inline-block;
	background: -webkit-gradient(linear, left bottom, left top, from(rgb(157, 255, 157)), to(rgb(153, 162, 166)));
	background: -webkit-linear-gradient(bottom, rgb(157, 255, 157), rgb(153, 162, 166));
	background: linear-gradient(to top, rgb(157, 255, 157), rgb(153, 162, 166));
	border-style: none;
	border-radius: 15px;
	padding: 30px;
	margin: 7px;
	box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
	-webkit-transition: -webkit-transform 0.5s linear;
	transition: -webkit-transform 0.5s linear;
	transition: transform 0.5s linear;
	transition: transform 0.5s linear, -webkit-transform 0.5s linear;
}

.robo-card:hover {
	-webkit-transform: scale(1.05);
	        transform: scale(1.05);
}

.robo-card > img {
	width: 100%;
	height: auto;
}

@media only screen and (max-width: 350px) {
    .robo-card {
        max-width: 250px;
    }
}


@font-face {
font-family: 'SEGA LOGO FONT';
font-style: normal;
font-weight: normal;
src: local('SEGA LOGO FONT'), url(/RoboFriends/static/media/SEGA.12a3db8b.woff) format('woff');
}

body {
	text-align: center;
}

.header {
	margin: 10px;
	display: flex;
	flex-wrap: wrap;
	flex-direction: column;
	align-items: center;
}

h1 {
	font-family: 'SEGA LOGO FONT';
