.robo-card {
	text-align: center;
	display: inline-block;
	background: linear-gradient(to top, rgb(157, 255, 157), rgb(153, 162, 166));
	border-style: none;
	border-radius: 15px;
	padding: 30px;
	margin: 7px;
	box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
	transition: transform 0.5s linear;
}

.robo-card:hover {
	transform: scale(1.05);
}

.robo-card > img {
	width: 100%;
	height: auto;
}

@media only screen and (max-width: 350px) {
    .robo-card {
        max-width: 250px;
    }
}

